<template>
  <div class="main-container" id="app">
    <div id="header">
      <img id="logo" src="img/logo.png" />
      <media :query="{ minWidth: 516 }">
        <div v-if="!address" id="connectWallet" @click="connect">
          Connect Wallet
        </div>
        <div v-else id="connectWallet" @click="disconnect">
          Disconnect Wallet
          <div class="address">{{ formatAddress(address) }}</div>
        </div>
      </media>
      <media :query="{ maxWidth: 515 }">
        <div v-if="!address" id="connectWallet" @click="menuOpen = !menuOpen">
          Menu
        </div>
        <div v-else id="connectWallet" @click="menuOpen = !menuOpen">
          Menu
          <div class="address">{{ formatAddress(address) }}</div>
        </div>
      </media>
      <div v-if="menuOpen" class="mobile-menu">
        <div
          @click="
            active = 0;
            menuOpen = !menuOpen;
          "
        >
          Overview
        </div>
        <div
          @click="
            active = 1;
            menuOpen = !menuOpen;
          "
          v-if="address"
        >
          Your Reward
        </div>
        <div
          @click="
            active = 2;
            menuOpen = !menuOpen;
          "
          v-if="address"
        >
          Process Fund
        </div>
        <div
          @click="
            active = 5;
            menuOpen = !menuOpen;
          "
        >
          Earn Reward
        </div>
        <div v-if="!address" @click="connect">Connect Wallet</div>
        <div v-else @click="disconnect">Disconnect Wallet</div>
      </div>
      <div v-if="menuOpen" class="menuBack" @click="menuOpen = false"></div>
    </div>
    <div id="content" ref="content">
      <div id="tabs">
        <div class="tabs">
          <div
            class="tab content-trigger"
            v-bind:class="active == 0 ? 'active' : null"
            data-trigger="overview"
            @click="active = 0"
          >
            Overview
          </div>
          <div v-if="address">
            <div
              id="stake"
              class="tab content-trigger connected"
              data-trigger="stake"
              v-bind:class="active == 1 ? 'active' : 'null'"
              @click="active = 1"
            >
              Your Reward
            </div>
            <div
              id="harvest"
              class="tab content-trigger connected"
              data-trigger="harvest"
              v-bind:class="active == 2 ? 'active' : 'null'"
              @click="active = 2"
            >
              Process Fund
            </div>
          </div>
          <div
            class="tab content-trigger"
            data-trigger="info"
            v-bind:class="active == 5 ? 'active' : 'null'"
            @click="active = 5"
          >
            Earn Reward
          </div>
        </div>
        <div class="tabs-mobile">
          <div
            class="tab content-trigger"
            v-bind:class="active == 0 ? 'active' : null"
            data-trigger="overview"
            v-if="active == 0"
          >
            Overview
          </div>
          <div
            id="stake"
            class="tab content-trigger connected"
            data-trigger="stake"
            v-bind:class="active == 1 ? 'active' : 'null'"
            v-if="active == 1"
          >
            Your Reward
          </div>
          <div
            id="harvest"
            class="tab content-trigger connected"
            data-trigger="harvest"
            v-bind:class="active == 2 ? 'active' : 'null'"
            v-if="active == 2"
          >
            Process Fund
          </div>
          <div
            class="tab content-trigger"
            data-trigger="info"
            v-bind:class="active == 5 ? 'active' : 'null'"
            v-if="active == 5"
          >
            Earn Reward
          </div>
        </div>
        <div class="content">
          <div
            id="overview_content"
            class="tab_content"
            v-bind:class="active == 0 ? 'active' : 'null'"
          >
            <div class="disconnected">
              <h2>Welcome to Perks Protocol reward dashboard.</h2>
              <p class="stats stats1">
                Total Reward:<br />
                <strong
                  ><span class="rewards_total">{{
                    numberFormat(
                      formatBalance(claimedReward) +
                        formatBalance(unClaimedReward)
                    )
                  }}</span>
                  PERKS</strong
                >
              </p>
              <p class="stats stats1">
                Unclaimed Reward:<br />
                <strong
                  ><span class="rewards_total">{{
                    numberFormat(formatBalance(unClaimedReward))
                  }}</span>
                  PERKS</strong
                >
              </p>
              <p class="stats">
                Claimed Reward:<br />
                <strong
                  ><span class="rewards_total">{{
                    numberFormat(formatBalance(claimedReward))
                  }}</span>
                  PERKS</strong
                >
              </p>
              <p class="stats stats1">
                Liquidity Reserve:<br />
                <strong
                  ><span class="rewards_total">{{
                    numberFormat(formatBalance(liquidityFund))
                  }}</span>
                  PERKS</strong
                >
              </p>
              <p class="stats stats1">
                Marketing Reserve:<br />
                <strong
                  ><span class="rewards_total">{{
                    numberFormat(formatBalance(marketingFund))
                  }}</span>
                  PERKS</strong
                >
              </p>
              <p class="stats">
                Funding Reward:<br />
                <strong
                  ><span class="rewards_total">{{
                  
                  numberFormat(fundingReward())
                  
                  }}</span>
                  PERKS</strong
                >
              </p>
              <p v-if="!address" class="su-msg">
                Please
                <span class="connect-trigger trigger-button" @click="connect"
                  >connect your wallet</span
                >
                to get started.
              </p>
            </div>
          </div>
          <div
            id="stake_content"
            class="tab_content"
            v-bind:class="active == 1 ? 'active' : 'null'"
          >
            <div class="connected">
              <p>
                Wallet Balance:
                <strong
                  ><span> {{ numberFormat(formatBalance(balance)) }} </span
                  >PERKS</strong
                >
                <a
                  class="buy-more"
                  href="https://pancakeswap.finance/swap?chain=bscTestnet&outputCurrency=0x2894A01aBF56b3B1756081a290FBaC32edF3B497"
                  target="_blank"
                  ><i class="fas fa-wallet1"></i> Buy PERKS</a
                >
              </p>
              <p>
                Unclamed Reward:
                <strong
                  ><span>{{
                    numberFormat(formatBalance(userData?.userRewardBalance))
                  }}</span>
                  PERKS</strong
                >
              </p>
              <p>
                Next Claim:
                <span class="countdown">{{ toHHMMSS(countDown) }}</span>
                <a
                  class="buy-more"
                  v-bind:class="[
                    countDown === 0 &&
                    formatBalance(userData?.userRewardBalance) !== 0
                    && userData?.userNextClaim !== '0'
                      ? ''
                      : 'buy-more-disabled',
                  ]"
                  @click="
                    ModalActive =
                      countDown === 0 &&
                      formatBalance(userData?.userRewardBalance) !== 0
                      && userData?.userNextClaim !== '0'
                        ? true
                        : false
                  "
                  ><i class="fas fa-wallet1"></i> Claim PERKS</a
                >
              </p>
              <p>
                Total Claimed:
                <strong
                  ><span>{{
                    numberFormat(formatBalance(userData?.userClaimedReward))
                  }}</span>
                  PERKS</strong
                >
              </p>
            </div>
          </div>
          <div
            id="harvest_content"
            class="tab_content"
            v-bind:class="active == 2 ? 'active' : 'null'"
          >
            <div class="connected">
              <h3 class="earn-reward">
                Earn PERKS by processing project funding.
              </h3>
              <div class="next-process">
                <p class="next-process-p">
                  Next Process:
                  <span class="countdown">{{
                    toHHMMSS(countDownProcess)
                  }}</span>
                </p>
                <button
                  id="harvest_confirm"
                  class="action_confirm"
                  v-bind:class="[
                    !formatBalance(requireFund) ||
                    !(
                      formatBalance(liquidityFund) >=
                        formatBalance(requireFund) ||
                      formatBalance(marketingFund) >= formatBalance(requireFund)
                    ) ||
                    isLoadingFund ||
                    countDownProcess !== 0 ||
                    userData?.userNextProcess === '0'
                      ? 'buy-more-disabled'
                      : '',
                  ]"
                  :disabled="
                    !formatBalance(requireFund) ||
                    !(
                      formatBalance(liquidityFund) >=
                        formatBalance(requireFund) ||
                      formatBalance(marketingFund) >= formatBalance(requireFund)
                    ) ||
                    isLoadingFund ||
                    countDownProcess !== 0 ||
                    userData?.userNextProcess === '0'
                  "
                  @click="processfund"
                >
                  Process Fund
                  <div v-if="isLoadingFund" class="spinner"></div>
                </button>
              </div>
              <p>
                Funding Reward:
                <strong>{{ numberFormat(fundingReward()) }} PERKS</strong>
              </p>
              <p class="earn-title">Funding Summary</p>


              <p>
                Funding Threshold:
                <strong
                  >{{ numberFormat(formatBalance(fundThreshold)) }} BNB</strong
                >
                <br />
              </p>
              <p>
                Required Fund:
                <strong
                  >{{ numberFormat(formatBalance(requireFund)) }} PERKS</strong
                >
                <br />
              </p>
              <p>
                Liquidity Reserve:
                <strong
                  >{{
                    numberFormat(formatBalance(liquidityFund))
                  }}
                  PERKS</strong
                >
                <br />
              </p>
              <p>
                Marketing Reserve:
                <strong
                  >{{
                    numberFormat(formatBalance(marketingFund))
                  }}
                  PERKS</strong
                >
                <br />
              </p>
            </div>
          </div>
          <div
            id="info_content"
            class="tab_content"
            style="width: 100%"
            v-bind:class="active == 5 ? 'active' : 'null'"
          >
            <h3 class="earn-reward">
              Perks Protocol is all about rewards. No taxes, no fees, no bots.
            </h3>

            <p class="earn-title">BUY PERKS</p>
            <p>
              You can earn rewards when you buy PERKS tokens. After each
              purchase, you can view your unclaimed reward balance under the
              <strong>'Your Reward'</strong> tab.<br /><br />There is a 24-hour
              claim interval after each purchase transaction. If you sell your
              Perks or transfer them before this interval, you will forfeit all
              your rewards.<br /><br />However, if you buy more PERKS, your
              unclaimed reward balance will be automatically compounded.<br />
              <br />Please check the next claim timer under the
              <strong>'Your Reward'</strong> tab before selling or transferring
              your tokens to another address.
            </p>

            <p class="earn-title">PROCESS FUND</p>
            <p>
              You can also earn more rewards by helping with liquidity and
              marketing fund processing. <br /><br />Other projects claim to do
              this automatically, but what they do not tell you is that they
              have to front-run your transaction in order to process the
              so-called automatic liquidity generation.<br /><br />Perks
              Protocol has opened up the process to any user willing to process
              the transaction. The user will automatically rewarded with 10% of
              the required fund amount.<br /><br />Go to the
              <strong>'Process Fund'</strong> tab to get started.<br /><br />Please
              note that there is also a 24-hour claiming interval for this
              transaction. Also, a user can only call this process once every 24
              hours to ensure fairness for all users.
            </p>

            <p class="earn-title">STAKING</p>
            <p>
              We will be adding the ability to stake your PERKS token to earn
              additional rewards. This will be included as part of our roadmap.
            </p>

            <p class="earn-title">PARTNERSHIPS</p>
            <p>
              We will also be partnering with other projects. Interacting with
              these projects will also earn you Perks rewards.
            </p>
          </div>
        </div>
      </div>
    </div>
    <Footer />
    <alert-modal :active="ModalActive" @ok="ModalActive = false" />
  </div>
</template>

<script>
// @ is an alias to /src
import Footer from "@/views/Footer";
import { mapGetters } from "vuex";
import Web3 from "@/helpers/Web3";
import Observer from "@/helpers/Observer";
import Utils from "@/helpers/Utils";
import AlertModal from "@/components/AlertModal.vue";
import { E_REJECT_SIGN, E_SIGNED } from "@/constants/events";
import Media from "vue-media";
import dateFormat from "dateformat";

export default {
  name: "Home",
  components: {
    // Navbar,
    Footer,
    Media,
    AlertModal,
  },
  beforeDestroy() {
    Observer.$off(E_REJECT_SIGN);
  },
  methods: {
    countDownTimer() {
      if (this.timer) {
        clearInterval(this.timer);
      }
      const handler = () => {
        this.countDown =
          new Date(this.userData.userNextClaim * 1000) - new Date().getTime();
        // console.log(this.countDown);

        if (this.countDown < 1000) {
          this.countDown = 0;
        }
      };
      handler();
      this.timer = setInterval(handler, 1000);

      if (this.timerProcess) {
        clearInterval(this.timerProcess);
      }
      const handlerProceess = () => {
        console.log(this.userData.userNextProcess);
        this.countDownProcess =
          new Date(this.userData.userNextProcess * 1000) - new Date().getTime();
        // console.log(this.countDown);

        if (this.countDownProcess < 1000) {
          this.countDownProcess = 0;
        }
      };
      handlerProceess();
      this.timerProcess = setInterval(handlerProceess, 1000);
    },
    async disconnect() {
      this.menuOpen = false;
      this.active = 0;
      await Web3.disconnect();
    },
    numberFormat(x) {
      var value = parseFloat(x).toLocaleString("en-US", {
        minimumFractionDigits: 0,
        maximumFractionDigits: 9,
      });
      // if(isNaN(value)) return "_"
      return value;
    },
    toHHMMSS(countDown) {
      let hours = Math.floor(
        (countDown % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
      );
      let minutes = Math.floor((countDown % (1000 * 60 * 60)) / (1000 * 60));
      let seconds = Math.floor((countDown % (1000 * 60)) / 1000);
      if (hours < 10) {
        hours = "0" + hours;
      }
      if (minutes < 10) {
        minutes = "0" + minutes;
      }
      if (seconds < 10) {
        seconds = "0" + seconds;
      }
      return hours + ":" + minutes + ":" + seconds;
    },
    fundingReward() {
      let rewardAmount = 0;
      if (
        this.formatBalance(this.liquidityFund) >=
        this.formatBalance(this.requireFund)
      )
        rewardAmount += this.formatBalance(this.requireFund);
      if (
        this.formatBalance(this.marketingFund) >=
        this.formatBalance(this.requireFund)
      )
      
      rewardAmount += this.formatBalance(this.requireFund);

      if(Number.isNaN(rewardAmount)){return 0}

      return (
        ((rewardAmount + this.formatBalance(this.userData?.userRewardBalance)) *
          this?.rateReward) /
        100
      );

    },
    toFixed(x_) {
      let x = x_;
      let e;
      if (Math.abs(x) < 1.0) {
        e = parseInt(x.toString().split("e-")[1]);
        if (e) {
          x *= Math.pow(10, e - 1);
          x = "0." + new Array(e).join("0") + x.toString().substring(2);
        }
      } else {
        e = parseInt(x.toString().split("+")[1]);
        if (e > 20) {
          e -= 20;
          x /= Math.pow(10, e);
          x += new Array(e + 1).join("0");
        }
      }
      return x;
    },
    format_Balance(temp) {
      // eslint-disable-next-line no-undef
      // return parseInt(BigInt(balance * 1000) / BigInt(10 ** decimals)) / 1000;
      if (temp >= 100) {
        return parseFloat(Number(temp.toString().match(/^\d+(?:\.\d{0,3})?/)));
      } else if (temp >= 10 && temp < 100) {
        return parseFloat(Number(temp.toString().match(/^\d+(?:\.\d{0,4})?/)));
      } else if (temp >= 1 && temp < 10) {
        return parseFloat(Number(temp.toString().match(/^\d+(?:\.\d{0,5})?/)));
      } else if (temp >= 0.1 && temp < 1) {
        return parseFloat(Number(temp.toString().match(/^\d+(?:\.\d{0,6})?/)));
      } else if (temp >= 0.01 && temp < 0.1) {
        return parseFloat(Number(temp.toString().match(/^\d+(?:\.\d{0,7})?/)));
      } else if (temp >= 0.001 && temp < 0.01) {
        return parseFloat(Number(temp.toString().match(/^\d+(?:\.\d{0,8})?/)));
      } else {
        return parseFloat(Number(temp.toString().match(/^\d+(?:\.\d{0,9})?/)));
      }
    },
    formatBalance(balance, decimals) {
      if (!balance) {
        return "_";
      }
      return Utils.formatBalance(balance, decimals);
    },
    formatAddress(address) {
      return Utils.formatAddress(address);
    },

    async processfund() {
      this.isLoadingFund = true;
      try {
        await Web3.processFund();
      } catch (e) {
        console.log("error", e);
      }
      this.isLoadingFund = false;
    },
    async connect() {
      this.menuOpen = false;
      if (this.isConnecting) {
        return;
      }
      try {
        await Web3.init();
        this.isConnecting = true;
      } catch (e) {
        console.log("error: ", e);
      }
    },
    formatDate(date) {
      return dateFormat(date, "yyyy/mm/dd HH:MM");
    },
  },
  computed: {
    ...mapGetters([
      "address",
      "balance",
      "staking",
      "reward",
      "total_reward",
      "isApproved",
      "claimedReward",
      "unClaimedReward",
      "liquidityFund",
      "marketingFund",
      "fundThreshold",
      "requireFund",
      "userData",
      "rateReward",
    ]),
  },
  watch: {
    async address() {
      if (!this.address) {
        return;
      }
      // Todo: set default eligible false
    },
  },

  async mounted() {
    await Web3.initTimer();

    Observer.$on(E_SIGNED, async () => {
      // await this.getHistory();
      this.countDownTimer();
    });
    Observer.$on(E_REJECT_SIGN, async () => {
      this.isConnecting = false;
      // console.log("disconnected");
    });
  },
  data() {
    return {
      isConnecting: false,
      countDown: 0,
      countDownProcess: 0,
      timer: null,
      timerProcess: null,
      ModalActive: false,
      active: 0,
      isLoadingFund: false,

      // Mobile Menu
      menuOpen: false,
    };
  },
};
</script>
