import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const Store = new Vuex.Store({
  state: {
    address: null,
    balance: 0,
    staking: 0,
    reward: 0,
    total_reward: 0,
    isApproved: false,
    claimedReward: 0,
    unClaimedReward: 0,
    liquidityFund: 0,
    marketingFund: 0,
    fundThreshold: 0,
    requireFund: 0,
    userData: [],
    rateReward: 0,
  },
  getters: {
    address: (state) => state.address,
    balance: (state) => state.balance,
    staking: (state) => state.staking,
    reward: (state) => state.reward,
    total_reward: (state) => state.total_reward,
    isApproved: (state) => state.isApproved,
    claimedReward: (state) => state.claimedReward,
    unClaimedReward: (state) => state.unClaimedReward,
    liquidityFund: (state) => state.liquidityFund,
    marketingFund: (state) => state.marketingFund,
    fundThreshold: (state) => state.fundThreshold,
    requireFund: (state) =>state.requireFund,
    userData: (state) => state.userData,
    rateReward: (state) => state.rateReward,
  },
  actions: {
  },
  mutations: {
    SET (state, [key, value]) {
      state[key] = value;
    }
  }
})

export default Store;