<template>
  <Modal :active="active" @close="handleOk">
    <div
      class=""
      id="staticBackdrop4"
      data-mdb-backdrop="static"
      data-mdb-keyboard="false"
      tabindex="-1"
      aria-labelledby="staticBackdropLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog-centered model-width1">
        <div class="">
          <div class="modal-body px-4">
            <div class="text-center">
              <div class="modal-title"><b>Claim PERKS Reward</b></div>
              <div class="modal-input">
                <div class="text-right">
                  <span class="claimprice">
                  {{ numberFormat(formatBalance(userData.userRewardBalance)) }}
                  PERKS</span></div>
                <div class="input-box">
                  <input v-model="claimAmount" placeholder="0" />
                  <span
                    class="balance value autofill"
                    @click="
                      claimAmount = formatBalance(userData.userRewardBalance)
                    "
                    >MAX</span
                  >
                </div>
              </div>
            </div>
            <div class="modal-button">
              <div class="modal-btn modal-cancel" @click="handleOk">Cancel</div>
              <button
                class="modal-btn"
                v-bind:class="[isLoadingClaim ? 'pRight' : '']"
                @click="handleClaim"
              >
                Claim
                <div v-if="isLoadingClaim" class="spinner"></div>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Modal>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from "vuex";
import Web3 from "@/helpers/Web3";
import Utils from "@/helpers/Utils";
import Modal from "./Modal.vue";
export default {
  name: "AlertModal",
  props: {
    callback: {},
    active: {
      type: Boolean,
    },
    btnOk: {
      type: String,
      default: "Yes",
    },
  },
  components: {
    Modal,
  },
  computed: {
    ...mapGetters(["userData"]),
  },
  watch: {},
  beforeDestroy() {},
  async mounted() {},
  methods: {
    numberFormat(x) {
      var value = parseFloat(x).toLocaleString("en-US", {
        minimumFractionDigits: 0,
        maximumFractionDigits: 9,
      });
      return value;
    },
    formatBalance(balance, decimals) {
      if (!balance) {
        return "_";
      }
      return Utils.formatBalance(balance, decimals);
    },
    handleOk() {
      if (this.callback) {
        this.callback();
      } else {
        this.$emit("ok");
      }
    },
    async handleClaim() {
      if (this.claimAmount <= 0) return;
      let isMax = false;
      if (
        this.claimAmount == this.formatBalance(this.userData.userRewardBalance)
      ) {
        isMax = true;
      }
      this.isLoadingClaim = true;
      try {
        await Web3.claim(this.claimAmount, isMax);
      } catch (e) {
        console.log("error", e);
      }
      this.isLoadingClaim = false;
      this.claimAmount = 0;
      this.handleOk();
    },
  },
  data() {
    return {
      claimAmount: 0,
      isLoadingClaim: false,
    };
  },
};
</script>

<style scoped></style>
