import Web3 from "web3";
import WalletConnectProvider from "@walletconnect/web3-provider";
const { formatBytes32String, parseBytes32String } = require("ethers/lib/utils");

import Web3Modal from "web3modal";

import Observer from "./Observer";
import store from "../store/index";
import {
  E_ACCOUNT_CHANGED,
  E_SIGNED,
  E_REJECT_SIGN,
} from "../constants/events";
import Config from "../config";
import Utils from "./Utils";
// import Parse from "./Parse";

import ABI from "../constants/abi";

const providerOptions = {
  walletconnect: {
    package: WalletConnectProvider, // required
    options: {
      // infuraId: Config.INFURA_ID, // required
      rpc: {
        [Config.CHAIN_ID]: Config.MAINNET_RPC,
      },
    },
  },
};

const web3Modal = new Web3Modal({
  cacheProvider: true,
  providerOptions, // required
});

class Web3Helper {
  constructor() {
    this.web3 = null;
    this.account = {};
    this.address = null;
    this.balance = 0;
    this.timer = null;
    this.provider = null;
    this.userRewardBalance = 0;
  }

  createAccount() {
    return this.web3.eth.accounts.create();
  }

  isAdmin() {
    return this.address.toLowerCase() == Config.ADMIN.toLowerCase();
  }

  async disconnect() {
    try {
      await web3Modal.clearCachedProvider();
    } catch (e) {
      console.log("error", e);
    }
    store.commit("SET", ["address", null]);
    // await this.saveStatus();
    this.address = null;
    clearInterval(this.timer);
    // localStorage.removeItem("isConnected");
    Observer.$emit(E_REJECT_SIGN);
  }

  async init() {
    const provider = await web3Modal.connect();
    this.provider = provider;
    this.web3 = new Web3(provider);
    // localStorage.setItem("isConnected", true);
    // await this.addToken();

    this.initObserver();

    const accounts = await this.web3.eth.getAccounts();

    // Subscribe to accounts change
    provider.on("accountsChanged", (accounts) => {
      // console.log('Account changed', accounts);
      Observer.$emit(E_ACCOUNT_CHANGED, accounts);
    });

    // Subscribe to chainId change
    provider.on("chainChanged", async (chainId) => {
      // console.log('Chain Changed', chainId);
      // eslint-disable-next-line no-undef
      if (BigInt(chainId) != BigInt(Config.CHAIN_ID)) {
        this.switchNetwork();
      }
    });

    // Subscribe to provider connection
    provider.on("connect", (info) => {
      console.log("Chain Connected", info.chainId);
    });

    // Subscribe to provider disconnection
    provider.on("disconnect", (error) => {
      console.log("Wallet Disconnected", error.code, error.message);
    });

    Observer.$emit(E_ACCOUNT_CHANGED, accounts);
  }

  async addToken() {
    try {
      // wasAdded is a boolean. Like any RPC method, an error may be thrown.
      const wasAdded = await this.provider.request({
        method: "wallet_watchAsset",
        params: {
          type: "ERC20", // Initially only supports ERC20, but eventually more!
          options: {
            address: "0x08351b9875721780c2e129a3a9ED1b60F29FaA58", // The address that the token is at.
            symbol: "Perks", // A ticker symbol or shorthand, up to 5 chars.
            decimals: 18, // The number of decimals in the token
            // image: tokenImage, // A string url of the token logo
          },
        },
      });

      if (wasAdded) {
        console.log("Thanks for your interest!");
      } else {
        console.log("Your loss!");
      }
    } catch (error) {
      console.log(error);
    }
  }

  async switchNetwork() {
    // eslint-disable-next-line no-undef
    const chainId = "0x" + BigInt(Config.CHAIN_ID).toString(16);
    if (this.isNetworkRequest) {
      return;
    }
    this.isNetworkRequest = true;
    try {
      await this.provider.request({
        method: "wallet_switchEthereumChain",
        params: [{ chainId }],
      });
    } catch (switchError) {
      // console.log('Switch Network Error', switchError);
      if (switchError.code === 4902) {
        try {
          await this.provider.request({
            method: "wallet_addEthereumChain",
            params: [
              {
                chainId: chainId,
                chainName: "Binance Smart Chain Testnet",
                nativeCurrency: {
                  name: "tBNB ",
                  symbol: "tBNB", // 2-6 characters long
                  decimals: 18,
                },
                rpcUrls: [Config.MAINNET_RPC],
                blockExplorerUrls: [Config.EXPLORER_LINK],
              },
            ],
          });
        } catch (addError) {
          // handle "add" error
          // console.log('Add Network Error', addError);
        }
      }
    }
    this.isNetworkRequest = false;
  }

  initObserver() {
    Observer.$off(E_ACCOUNT_CHANGED);

    Observer.$on(E_ACCOUNT_CHANGED, async (accounts) => {
      this.address = accounts[0];

      const chainId = await this.web3.eth.getChainId();
      if (chainId != Config.CHAIN_ID) {
        await this.switchNetwork();
      }

      const balance = await this.getTokenBalance();
      this.balance = balance;

      store.commit("SET", ["address", this.address]);
      store.commit("SET", ["balance", this.balance]);

      const userData = await this.getTokenContract()
        .methods.userData(this.address)
        .call();
      this.userRewardBalance = userData.userRewardBalance;
      store.commit("SET", ["userData", userData]);

      Observer.$emit(E_SIGNED);
    });
  }

  async initOverview() {
    this.web3 = new Web3("https://data-seed-prebsc-1-s3.binance.org:8545");

    const claimedReward = await this.getTokenContract()
      .methods.claimedFund()
      .call();
    const unClaimedReward = await this.getTokenContract()
      .methods.unclaimedFund()
      .call();
    const liquidityFund = await this.getTokenContract()
      .methods.liquidityFund()
      .call();
    const marketingFund = await this.getTokenContract()
      .methods.marketingFund()
      .call();
    const fundThreshold = await this.getTokenContract()
      .methods.liquifyFund()
      .call();

    const swapRouter = await this.getTokenContract()
      .methods.swapRouter()
      .call();

    // console.log(swapRouter);

    const WETHAddr = await this.getPancakeContract(swapRouter)
      .methods.WETH()
      .call();

    const rateReward = await this.getTokenContract()
      .methods.rateReward()
      .call();

    let requireFund;

    try {
      requireFund = await this.getPancakeContract(swapRouter)
        .methods.getAmountsIn(fundThreshold, [Config.TOKEN_ADDRESS, WETHAddr])
        .call();
    } catch (e) {
      console.log("No liquidity");
      requireFund = [Utils.formatBigInt(0)];
    }

    // console.log(WETHAddr, requireFund[0], rateReward / 100);

    // console.log(
    //   claimedReward,
    //   unClaimedReward,
    //   liquidityFund,
    //   marketingFund,
    //   fundThreshold
    // );

    store.commit("SET", ["claimedReward", claimedReward]);
    store.commit("SET", ["unClaimedReward", unClaimedReward]);
    store.commit("SET", ["liquidityFund", liquidityFund]);
    store.commit("SET", ["marketingFund", marketingFund]);
    store.commit("SET", ["fundThreshold", fundThreshold]);
    store.commit("SET", ["requireFund", requireFund[0]]);
    store.commit("SET", ["rateReward", rateReward]);
  }

  initTimer() {
    if (this.timer) {
      clearInterval(this.timer);
    }
    const handler = async () => {
      // console.log("updateBalance");
      await this.initOverview();
      await this.updateBalance();
    };
    handler();
    this.timer = setInterval(handler, 30000);
  }

  async initAccount() {
    this.initTimer();
    Observer.$emit(E_SIGNED);
  }

  bytes32(str) {
    return formatBytes32String(str);
  }

  parseBytes32(bytes) {
    return parseBytes32String(bytes);
  }

  isAddress(address) {
    return this.web3.utils.isAddress(address);
  }

  async getBalance(address) {
    return this.web3.eth.getBalance(address);
  }

  async getTokenBalance() {
    const web3 = new Web3(new Web3.providers.HttpProvider(Config.MAINNET_RPC));
    const tokenContract = new web3.eth.Contract(
      ABI.PerksABI,
      Config.TOKEN_ADDRESS
    );
    const balance = await tokenContract.methods.balanceOf(this.address).call();
    return balance;
  }

  async updateBalance() {
    if (!this.address) return;

    const balance = await this.getTokenBalance();
    this.balance = balance;

    store.commit("SET", ["balance", this.balance]);

    const userData = await this.getTokenContract()
      .methods.userData(this.address)
      .call();
    this.userRewardBalance = userData.userRewardBalance;
    console.log("userData", userData);
    store.commit("SET", ["userData", userData]);
  }

  sign(address, nonce) {
    const message = this.web3.eth.accounts.hashMessage(
      `I am signing my one-time nonce: ${nonce}`
    );
    return this.web3.eth.sign(message, address);
  }

  async processFund() {
    // this.web3 = new Web3("https://data-seed-prebsc-1-s3.binance.org:8545");
    this.web3 = new Web3(window.ethereum);

    await this.getTokenContract().methods.processFunding().send({
      from: this.address,
    });
    await this.initOverview();
    await this.updateBalance();
  }

  async claim(amount, isMax) {
    this.web3 = new Web3(window.ethereum);

    if (!isMax) {
      await this.getTokenContract()
        .methods.claimReward(Utils.formatBigInt(amount * 10 ** 18))
        .send({
          from: this.address,
        });
    } else {
      await this.getTokenContract()
        .methods.claimReward(this.userRewardBalance)
        .send({
          from: this.address,
        });
    }

    await this.initOverview();
    await this.updateBalance();
  }

  getTokenContract(address) {
    if (!address) {
      address = Config.TOKEN_ADDRESS;
    }
    return new this.web3.eth.Contract(ABI.PerksABI, address);
  }

  getPancakeContract(address) {
    return new this.web3.eth.Contract(
      ABI.RouterABI,
      Web3.utils.toChecksumAddress(address)
    );
  }

  async decimals(address) {
    if (!address) {
      address = Config.TOKEN_ADDRESS;
    }
    const tokenContract = this.getTokenContract();
    return parseInt(await tokenContract.methods.decimals().call());
  }

  async getGasPrice() {
    return await this.web3.eth.getGasPrice();
  }

  async estimateGasLimit(option) {
    // from, to, data, value
    return await this.web3.eth.estimateGas(option);
  }

  async send(transaction, privateKey, options) {
    // from, to, gas: gasLimit, value, gasPrice / (maxFeePerGas, maxPriorityFeePerGas)
    if (transaction) {
      options.data = transaction.encodeABI();
    }
    const signedTx = await this.web3.eth.accounts.signTransaction(
      options,
      privateKey
    );

    const result = await this.web3.eth.sendSignedTransaction(
      signedTx.rawTransaction
    );
    result.hash = result.blockHash;
    return result;
  }
}

const helper = new Web3Helper();
// helper.init();
export default helper;
